<template>
    <div class="pick-wrap">
        <div v-title :data-title="title">{{title}}</div>
        <pick-header :text="title"></pick-header>
        <div class="teacher-wrap clearfix">
            <img v-lazy="dataImg" class="fl">
            <div class="text-main fl">
                <p class="int">爱股轩名师</p>
                <h3 class="name">廖英强</h3>
                <p class="brif">用技术打开股市财富之门</p>
            </div>
            <div class="icon fl"></div>
        </div>
        <div class="tab-wrap">
            <ol class="page clearfix">
                <li class="fl"></li>
                <li class="fl"></li>
            </ol>
            <div class="content-main">
                <div></div>
                <div></div>
            </div>
        </div>
    </div>
</template>
<script>
    import PickHeader from './children/header.vue'
    export default {
        name : 'pickweek',
        data(){
            return {
                title:'名师推荐商品',
                dataImg:require('@/assets/con1.jpg')
            }
        },
        components:{
            PickHeader
        }
    }
</script>
<style lang="less" scoped>
    .teacher-wrap{
        padding:.2rem;
        position:relative;
        img{
            width:1.64rem;
            height:1.64rem;
            border-radius:50%;
        }
        .text-main{
            padding-left:.35rem;
            .int{
                font-size:.24rem;
                color:#999;
                padding-top:.18rem;
            }
            .name{
                font-size:.32rem;
                color:#000;
                font-weight:normal;
                padding-top:.1rem;
            }
            .brif{
                font-size:.28rem;
                color:#666;
                padding-top:.2rem;
            }
        }
        .icon{
            width:.6rem;
            height:.87rem;
            background:url(../../assets/icon.png) no-repeat;
            background-size:100% 100%;
            position:absolute;
            top:50%;
            right:.4rem;
            margin-top:-.435rem;
        }
    }
</style>


